.price {
  text-align: center;
  text-transform: uppercase;
}

.price .plan .header {
  padding: 40px 20px;

  border-top: 1px solid var(--main-color);
  border-bottom: 1px solid var(--main-color);
}
.price .plan .header h3 {
  font-weight: normal;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.price .plan .header span {
  font-size: 60px;
  font-weight: bold;
}
.price .plan .header span::before {
  content: "$";
  position: relative;
  font-size: 25px;
  top: -40px;
  margin-right: 15px;
  font-weight: normal;
}
.price .plan .header span::after {
  content: "/MO";
  position: relative;
  font-size: 20px;
  right: -15px;
  font-weight: normal;
}

.price .plan .list-unstyled {
  border-bottom: 1px solid var(--main-color);
}
.price .plan .list-unstyled li {
  padding: 20px;
  position: relative;
}

.price .plans .plan ul li:not(:last-child)::after {
  content: "";
  position: absolute;
  width: 140px;
  height: 1px;
  background-color: var(--main-color);
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
}
.price .plans .plan .foot {
  margin-bottom: 50px;
}
/*=============================================*/
.subscribe {
  margin-top: 50px;
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  background-image: url("../imgs/subscribe.jpg");
  background-size: cover;
  color: white;
  position: relative;
}
.subscribe::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 40%);
}

.subscribe .sub-text,
.subscribe .sub-form {
  position: relative;
  z-index: 5;
  text-align: left;
  margin: 10px;
}
