.about .about-img {
  width: 100%;
  text-align: center;
  z-index: -1;
  position: relative;
  bottom: -150px;
}

@media only screen and (max-width: 767px) {
  .about .about-img img {
    width: 100%;
  }
  .about .about-img {
    position: relative;
    bottom: -60px;

    left: 50%;
    transform: translateX(-50%);
    width: 80%;
  }
}

.about-state {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  background-image: url("../imgs/stats.png");
  background-size: cover;
  position: relative;
}
.about-state::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 60%);
}
.about-state .states {
  padding: 30px;
  position: relative;
  text-align: center;
  color: white;
  background-color: var(--transparent-color);
  border-radius: 5px;
  box-shadow: white 0px 0px 6px 0.3px;
  overflow: hidden;
}

.about-state .icon {
  background-color: black;
  padding: 4px 8px;
  display: inline-block;
  width: 33px;
  height: 33px;
  border-radius: 50%;
}
.about-state .number {
  font-size: 50px;
  font-weight: bold;
}
