.font-size-18 {
  font-size: 18px;
  line-height: 1.6em;
}

.my-slide {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  width: 100%;
  height: 150px;
}
.my-slide-cards {
  background-color: #19c8fa4d;
  height: 40vh;
  width: 60%;
  display: flex;
  justify-content: flex-end;
  color: white;
}

.my-slide-cards .my-card {
  flex-basis: 70%;
  padding: 10px;
}

@media only screen and (min-width: 300px) and (max-width: 1000px) {
  .my-slide-cards {
    height: 40vh;
    width: 80%;
    margin: 0px 10%;
    font-size: 16px;
    padding: 10px;
  }
  .my-slide-cards .my-card {
    flex-basis: 100%;
  }
}
