.services-item .serv-box {
  margin-bottom: 40px;
}
.services-item .serv-box h3 {
  color: var(--main-color);
}

@media only screen and (max-width: 580px) {
  .services-item .serv-box .row {
    text-align: center;
    font-size: 16px;
  }
}

.design {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
  height: 600px;
  background-image: url("../imgs/design-features.jpg");
  background-size: cover;
  position: relative;
  overflow: hidden;
  margin-bottom: 40px;
}
.design .container {
  width: 100%;
}
.design::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 60%);
}

.design .img-design {
  position: relative;
  z-index: 2;
  bottom: -100px;
  left: 80px;
}
.design .text-design {
  background-color: var(--transparent-color);
  position: relative;
  color: white;
  z-index: 2;
  padding: 40px;
}
.design .text-design ul {
  margin-top: 40px;
}
.design .text-design ul li {
  background-color: inherit;
  color: white;
  border: none;
  padding-left: 5px;
}

.design .text-design h2 {
  position: relative;
}

.design .text-design h2::after {
  content: "";
  position: absolute;
  left: -25px;
  top: 0;
  border: 3px solid var(--main-color);
  height: 40px;
  background-color: red;
}
.design .text-design h2::before {
  content: "";
  position: absolute;
  left: -15px;
  top: 0;
  border: 3px solid var(--main-color);
  height: 40px;
  background-color: red;
  transform: translateY(15px);
}
.design .text-design svg {
  color: var(--main-color);
  margin-right: 10px;
}
@media only screen and (max-width: 730px) {
  .design .img-design {
    visibility: hidden;
  }
  .design .text-design {
    top: -300px;
  }
}
