.section-title {
  margin-top: 50px;
  margin-bottom: 50px;
  text-transform: uppercase;
  height: 30vh;
  text-align: center;
}
.section-title .header {
  position: relative;
  display: inline-block;
  margin-bottom: 50px;
}
.section-title .header::after {
  content: "";
  position: absolute;
  width: 80%;
  left: 50%;
  bottom: -15px;
  transform: translate(-50%, -50%);
  height: 2px;
  background-color: black;
}
.section-title .header::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  left: 0%;
  bottom: -28px;
  transform: translate(5%, -50%);
  background-color: white;
  border-radius: 50%;
  border: 1px solid black;
  z-index: 2;
  animation: animate 1.5s infinite alternate linear;
}

@keyframes animate {
  30% {
    left: 30%;
    background-color: var(--main-color);
    border: var(--main-color);
  }
  60% {
    left: 60%;
    background-color: var(--transparent-color);
    border: var(--transparent-color);
  }
  100% {
    left: 90%;
  }
}
