.landing {
  min-height: 100vh;
  /* background-color: #1f2021; */
  background-image: url("../imgs/landing.jpg");
  background-size: cover;
  position: relative;
}
.landing::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 60%);
}
