.contact {
  padding-bottom: var(--section-padding);
  text-transform: uppercase;
}

.contact .myform .form-group {
  margin: 10px;
}

.contact .myform .form-group input {
  width: 100%;
  height: 10vh;
}

.contact .myform .mybtn {
  text-align: right;
  margin: 10px;
}
.contact .number-address {
  display: flex;
  flex-wrap: wrap;
  gap: 70px 180px;
}
.contact .number-address h4 {
  text-transform: uppercase;
  margin-bottom: 19px;
  font-weight: bold;
  font-size: 18px;
}
.contact .number-address span {
  color: #aaa;
  font-family: italic;
}

.contact .number-address .numbers .phone {
  display: block;
}
