.skills-testimonials {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
}
.skills-testimonials .section-title h1 {
  font-size: 20px;
}
.skills-testimonials .section-title p {
  font-size: 13px;
}
.skills-testimonials .testimonials .my-card {
  margin-bottom: 50px;
}
.skills-testimonials .testimonials .my-card img {
  width: 100%;
}
.skills-testimonials .testimonials .my-card img:hover {
  transform: rotate(5deg) scale(1.2);
  opacity: 0.7;
}
.skills-testimonials .testimonials .my-card .my-card-desc {
  border-bottom: 1px solid #aaa;
}
.skills-testimonials .testimonials .my-card .my-card-desc .author {
  text-align: right;
  color: #aaa;
}

@media only screen and (max-width: 580px) {
  .skills-testimonials .testimonials .my-card {
    text-align: center;
  }
  .skills-testimonials .testimonials .my-card img {
    width: 20%;
  }
}

/*==========================================*/
.skills-testimonials .skills .prog-holder {
  margin-bottom: 40px;
}

.skills-testimonials .skills .prog-holder h4 {
  margin-bottom: 15px;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 16px;
}
.skills-testimonials .skills .prog-holder .prog {
  background-color: #dadadc;
  height: 30px;
}
.skills-testimonials .skills .prog-holder .prog span {
  display: block;
  background-color: var(--main-color);
  height: 100%;
  position: relative;
}
.adobe {
  width: 90%;
  animation: progress 3s 1.5s linear alternate-reverse;
}
.html {
  width: 85%;
  animation: progress 3s 1.5s linear alternate-reverse;
}
.js {
  width: 80%;
  animation: progress 3s 1.5s linear alternate-reverse;
}
.php {
  width: 90%;
  animation: progress 3s 1.5s linear alternate-reverse;
}
@keyframes progress {
  50% {
    width: 50%;
    background: greenyellow;
  }
  100% {
    width: 0%;
    background: var(--transparent-color);
  }
}
.skills-testimonials .skills .prog-holder .prog span::before {
  content: attr(data-progress);
  position: absolute;
  background-color: black;
  color: white;
  top: -45px;
  right: -20px;
  padding: 4px 0;
  width: 40px;
  text-align: center;
  border-radius: 4px;
}

.skills-testimonials .skills .prog-holder .prog span::after {
  content: "";
  position: absolute;
  border-style: solid;
  border-width: 8px;
  border-color: black transparent transparent transparent;
  right: -8px;
  top: -15px;
}
/*==========================================*/

.quote {
  background-image: url("../imgs/quote.jpg");
  background-size: cover;
  position: relative;
  color: white;
  margin-top: 80px;
}
.quote::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 70%);
}
.quote .container {
  position: relative;
}
.quote q {
  padding: 0 50px;
  font-size: 30px;
  margin-bottom: 20px;
  display: block;
  line-height: 1.6;
}
.quote .container q::before {
  font-family: "Font Awesome 5 Free";
  content: open-quote;
  font-weight: 600;
  font-size: 50px;
  margin-right: 10px;
  position: relative;
  top: -10px;
}
.quote .container q::after {
  font-family: "Font Awesome 5 Free";
  content: close-quote;
  font-weight: 600;
  font-size: 50px;
  margin-left: 10px;
  position: relative;
  top: 10px;
}
