.portofolio .row > * {
  padding-right: 0;
  padding-left: 0;
}

.portofolio .video {
  margin-top: 160px;
}
.portofolio .shuffle img {
  max-width: 100%;
  transition: 0.3s;
}
.portofolio .shuffle:hover img {
  transform: rotate(3deg) scale(1.1);
  opacity: 0.6;
}
.portofolio .shuffle {
  padding: 0;
  overflow: hidden;
  position: relative;
  transition: all 0.5s ease-in-out;
}
.portofolio .shuffle .caption {
  position: absolute;
  left: 0;
  padding: 10px;
  background-color: white;
  width: 100%;
  transition: 0.6s;
  bottom: -100%;
}
.portofolio .shuffle:hover .caption {
  bottom: 0;
}
@media only screen and (max-width: 600px) {
  .portofolio .shuffle {
    text-align: center;
  }
  .portofolio .shuffle img {
    max-width: 100%;
    max-height: 250px;
    transition: 0.3s;
  }
  .portofolio .shuffle:hover img {
    transform: rotate(1deg) scale(1);
    opacity: 0.6;
  }
}

.video {
  position: relative;
}
.video video {
  width: 100%;
}
.video::before {
  content: "";
  width: 100%;
  height: 99.5%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 40%);
}
.video .text {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--transparent-color);
  color: white;
  text-align: center;
  padding: 50px;
  font-size: 20px;
}
