.footer {
  padding-top: 50px;
  padding-bottom: 20px;
  background-image: url("../imgs/subscribe.jpg");
  position: relative;
  background-size: cover;
}
.footer::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 70%);
}

.footer .centered-item {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 5;
  color: white;
}
.footer .centered-item div {
  text-align: center;
  margin: 20px;
}
.footer .centered-item .footer-content .social p {
  font-size: 26px;
  border-bottom: 1px solid white;
}

.footer .centered-item .footer-content .social svg {
  margin: 10px;
  font-size: 26px;
}
.footer .centered-item .footer-content .copyright {
  font-size: 20px;
}

.footer .centered-item .footer-content .copyright span {
  color: var(--main-color);
  font-size: 26px;
}
